import empty from "@/components/empty";
export default {
	components: {
		empty
	},
	data() {
		return {
			list: []
		}
	},
	onLoad() {
		this.getList()
	},
	methods: {
		getList() {
			return this.$http.get('user.favorite').then(res => {
				this.list = res.data
			}).catch(e => {})
		},
		toDetail(item) {
			uni.navigateTo({
				url: '/pages/product/product?id=' + item.product.id
			})
		}
	},
	onPullDownRefresh() {
		this.getList().then(res => {
			uni.stopPullDownRefresh()
		}).catch(e => {
			uni.stopPullDownRefresh()
		})
	}
}
